<template>
  <a-modal
    :visible="true"
    :closable="false"
    :title="null"
    :footer="null"
    width="380px"
  >
    <a-form
      wrappedComponentRef="form"
      id="Form"
    >
      <a-form-item v-bind="validateInfos.user">
        <a-input
          class="input"
          placeholder="账号 / 邮箱"
          v-model:value="userInfo.user"
          @blur="onUserInputBlur"
        >
          <template v-slot:prefix>
            <UserOutlined class="user-icon" />
          </template>
        </a-input>
      </a-form-item>
      <a-form-item v-bind="validateInfos.password">
        <a-input
          class="input"
          type="password"
          v-model:value="userInfo.password"
          placeholder="Password"
        >
          <template v-slot:prefix>
            <LockOutlined class="lock-icon" />
          </template>
        </a-input>
      </a-form-item>
      <a-form-item>
        <a-checkbox>
          记住我
        </a-checkbox>
        <a class="forgot-pass" href="">
          忘记密码
        </a>
        <a-button type="primary" html-type="submit" @click="onSumbit" class="login-btn">
          登录
        </a-button>
        <a href="">
          马上注册！
        </a>
      </a-form-item>
    </a-form>
  </a-modal>
</template>

<script lang="ts">
import { UserOutlined, LockOutlined } from '@ant-design/icons-vue';
import { useForm } from '@ant-design-vue/use';

import { reactive } from 'vue';
import { useUser } from '../context/user_context';

export default {
  components: {
    UserOutlined,
    LockOutlined,
  },
  setup() {
    const userInfo = reactive({ user: '', password: '' });
    const validateRules = reactive({
      user: [
        {
          required: true,
          message: '请输出用户名',
        },
        {
          min: 3,
          message: '用户名长度必须大于3个字符',
          trigger: 'blur',
        },
      ],
      password: [
        {
          required: true,
          message: '请输入密码',
        },
      ],
    });
    const { resetFields, validate, validateInfos } = useForm(userInfo, validateRules);
    const { loginByPassword } = useUser() || {};
    const onSumbit = (e: Event) => {
      e.preventDefault();
      validate().then(() => {
        if (loginByPassword) {
          loginByPassword(userInfo.user, userInfo.password);
        }
      }).catch(() => {
        console.log('Fail to login!'); // eslint-disable-line
      });
    };
    const onUserInputBlur = () => {
      validate('user', { trigger: 'blur' });
    };
    return {
      userInfo,
      resetFields,
      validate,
      validateInfos,
      onSumbit,
      onUserInputBlur,
    };
  },
};
</script>

