<template>
  <div v-if="user && user.isLogin" >
    欢迎，{{ user.name }}
  </div>
  <div v-else>
    <a-button type="primary" @click="openDialog">
      登录
    </a-button>
    <Login v-if="dialog.open" />
  </div>
</template>

<script lang="ts">
import { reactive } from 'vue';
import { useUser } from '../context/user_context';
import Login from './Login.vue';

export default {
  components: {
    Login,
  },
  setup() {
    const dialog = reactive({ open: false });
    const { user } = useUser() || {};
    const openDialog = () => {
      dialog.open = true;
    };
    return {
      dialog,
      user,
      openDialog,
    };
  },
};
</script>

